<template>
    <div id="spine-div" class="row" v-show="isSpine">
        <ul class="nav nav-tabs fs-5">
        <li class="nav-item" v-for="(key, index) in unit.Fullbody" :key="index">
            <a class="nav-link" @click="setSpine(unit.Fullbody[index])" href="#" v-text="`Skin${index}`" v-if="unit.Fullbody[index] != null"></a>
        </li>
        <li class="nav-item ms-auto">
            <button class="btn fs-5" @click="isSpine = !isSpine">
                <i class="bi bi-x-lg"></i>
            </button>
        </li>
        </ul>
        <div id="spine_FULL" class="col"></div>
    </div>
</template>

<script>
/* eslint-disable */
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'

export default {
    name: 'SpineAnimation',
    components: {
    },
    setup() {
        const {
            unit,
            isSpine,
        } = storeToRefs(useSpine())

        const setSpine = (u) => {
            useSpine().setSpine('spine_FULL', u)
        }

        
        return {
            unit,
            isSpine,
            setSpine,   
        }
    }
}
</script>

<style scoped>
#spine-div {
    position: absolute;
    top: 5vh ;
    bottom: 5vh;
    left: 5vw;
    width: 90vw;
    height: 90vh;
    z-index: 5;
    background-color: rgba(255, 255, 255, 0.9);
}
@media screen and (max-width: 768px) {
    #spine-div {
        margin: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
    }
    #spine-div li {
        font-size: medium;
    }
}
</style>
