<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
            <table class="table table-hover table-dark rounded rounded-4 overflow-hidden">
                <tbody>
                    <tr>
                        <th rowspan="2" scope="col" style="width:20%;" class="align-middle">
                            <img :src="`img/UI_Card/${unit.Img_L}`" class="w-100" alt="">
                        </th>
                        <th scope="col">
                            <h4 v-text="text.background"></h4>
                        </th>
                    </tr>
                    <tr>
                        <td v-html="unit.Description[language]"></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
// import { ref, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'

export default {
    name: 'UnitDescription',
    components: {
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const {
            unit,
        } = storeToRefs(useInitUnit())
        return {
            language,
            isMobile,
            text,
            unit,
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.9;
    table-layout:fixed;
}
</style>
