<template>
    <div class="col-md-6 offset-md-1 col-11">
        <table class="table table-hover rounded rounded-4 overflow-hidden">
            <thead>
                <tr>
                    <th scope="col" style="width: 50%;">
                        <div class="d-inline-block">
                            <img class="img_star" src="img/UI_Icon_Star_Hero.png" alt="" v-for="index in unit.Teir" :key="index">
                        </div>
                    </th>
                    <th scope="col" class="d-flex">
                        <img src="img/UI_Icon_CombatCost.png" alt="" style="width: 1.5em;">
                        <h3 class="my-auto ms-3"><b v-text="unit.DP"></b></h3>
                        <button type="button" class="btn btn-outline-secondary ms-auto" @click="isDetail = !isDetail" v-show="!isDetail">
                            <i class="bi bi-plus-lg"></i>
                        </button>
                        <button type="button" class="btn btn-outline-secondary ms-auto" @click="isDetail = !isDetail" v-show="isDetail">
                            <i class="bi bi-dash-lg"></i>
                        </button>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <th scope="row" v-text="text.class"></th>
                    <td><span class="me-2"><img :src="`img/UI_Icon_Class_${unit.Class[0]}.png`" alt="" style="width: 2em;"></span><span v-text="unit.Class[language]"></span></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.element"></th>
                    <td><span class="me-2"><img :src="`img/UI_Icon_Attribute_${unit.Element[0]}.png`" alt="" style="width: 2em;"></span><span v-text="unit.Element[language]"></span></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.dmgType"></th>
                    <td v-text="unit.DamageType[language]"></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.level"></th>
                    <td>
                        <div class="input-group input-group-sm w-50">
                            <input type="number" aria-label="level" class="form-control" 
                            style="background: transparent; font-weight: bold;" 
                            min="1" v-model="level">
                        </div>
                    </td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.atk"></th>
                    <td class="d-flex justify-content-between"><span>{{ atk }}</span><span><b>{{ unit.rankATK }}/{{ rankLen }}</b></span></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.def"></th>
                    <td class="d-flex justify-content-between"><span>{{ def }}</span><span><b>{{ unit.rankDEF }}/{{ rankLen }}</b></span></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.hp"></th>
                    <td class="d-flex justify-content-between"><span>{{ hp }}</span><span><b>{{ unit.rankHP }}/{{ rankLen }}</b></span></td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.energyGain"></th>
                    <td>{{ unit.energyGainPerAttack }}</td>
                </tr>
                <tr>
                    <th scope="row" v-text="text.aspd"></th>
                    <td>{{ unit.attackSpeed }}</td>
                </tr>
                <template v-if="isDetail">
                    <tr>
                        <th scope="row" v-text="text.range"></th>
                        <td>{{ unit.attackRange }}</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.mspd"></th>
                        <td>{{ unit.moveSpeed }}</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.crit"></th>
                        <td>{{ unit.critChance }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.cdmg"></th>
                        <td>{{ unit.critDamage }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.eva"></th>
                        <td>{{ unit.baseEvasionPercent }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.acc"></th>
                        <td>{{ unit.baseAccuracyPercent }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.pres"></th>
                        <td>{{ unit.physResist }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.mres"></th>
                        <td>{{ unit.magicResist }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.resilience"></th>
                        <td>{{ unit.resilience }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.lifeSteal"></th>
                        <td>{{ unit.hpAbsorb }}%</td>
                    </tr>
                    <tr>
                        <th scope="row" v-text="text.healBonus"></th>
                        <td>{{ unit.healBonus }}%</td>
                    </tr>
                </template>
            </tbody>
        </table>
    </div>
</template>

<script>
/* eslint-disable */
import { ref } from 'vue'
import { computed } from '@vue/runtime-core'
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'
import Characters from '@/assets/json/Characters.json'

export default {
    name: 'UnitStat',
    components: {
    },
    setup() {
        const { language, text } = storeToRefs(useLanguage())
        const isDetail = ref(false)
        const level = ref(80)

        const { unit } = storeToRefs(useInitUnit())
        const rankLen = Characters.length
        console.log(unit.value)
        const atk = computed(() => {
            return Math.floor(unit.value.ATK * (1 + 0.15 * (level.value - 1)))
        })
        const def = computed(() => {
            return Math.floor(unit.value.DEF * (1 + 0.15 * (level.value - 1)))
        })
        const hp = computed(() => {
            return Math.floor(unit.value.HP * (1 + 0.15 * (level.value - 1)))
        })

        return {
            language,
            text,
            isDetail,
            unit,
            atk,
            def,
            hp,
            rankLen,
            level,
        }
    }
}
</script>

<style scoped>
.img_star {
    width: 2rem;
}
table {
    background-color: rgb(255, 255, 255, 0.5);
}
tbody>tr, thead>tr>th {
    vertical-align: middle;
}
@media screen and (max-width: 768px) {
    .img_star {
        width: 1.6rem;
    }
}</style>
