<template>
    <div class="row justify-content-center">
        <div class="col-md-auto offset-md-auto col-11">
        <table class="table table-hover table-dark rounded rounded-4 overflow-hidden">
            <thead>
                <tr>
                    <th scope="col" style="width: 30%;">
                        <h4 v-text="text.ability"></h4>
                    </th>
                    <th scope="col">
                        <div class="input-group d-flex justify-content-end" role="group">
                            <span class="input-group-text">{{ text.level }}:</span>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 1 }" @click="skillLevel = 1">1</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 2 }" @click="skillLevel = 2">2</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 3 }" @click="skillLevel = 3">3</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 4 }" @click="skillLevel = 4">4</button>
                            <button type="button" class="btn btn-outline-light btn-sm" :class="{ active: skillLevel === 5 }" @click="skillLevel = 5">5</button>
                        </div>
                    </th>
                </tr>
            </thead>
            <tbody v-if="isMobile">
                <tr v-if="unit.ActiveName.length" class="table-group-divider">
                    <th scope="row" colspan="2">
                        <div class="d-flex">
                            <span v-text="unit.ActiveName[language]"></span>
                            <span>&nbsp;/&nbsp;</span>
                            <span v-text="text.active"></span>
                            <span v-text="unit.cooldown" class="text-info ms-auto"></span>
                            <span v-text="text.cd" class="text-info"></span>
                        </div>
                    </th>
                </tr>
                <tr v-if="unit.ActiveName.length">
                    <td scope="row" colspan="2" v-html="unit.ActiveDescription[language][skillLevel - 1]"></td>
                </tr>
                <tr v-if="unit.PassiveName.length" class="table-group-divider">
                    <th scope="row" colspan="2">
                        <span v-text="unit.PassiveName[language]"></span>
                        <span> / </span>
                        <span v-text="text.passive"></span>
                    </th>
                </tr>
                <tr v-if="unit.PassiveName.length">
                    <td scope="row" colspan="2" v-html="unit.PassiveDescription[language][skillLevel - 1]"></td>
                </tr>
                <tr>
                    <th scope="col" colspan="2" style="width: 30%;">
                        <h4 v-text="text.trait"></h4>
                    </th>
                </tr>
                <template v-for="(key, index) in unit.TraitsName" :key="index">
                    <tr class="table-group-divider">
                        <th scope="row" colspan="2">{{ unit.TraitsName[index][language] }}</th>
                    </tr>
                    <tr>
                        <td scope="row" colspan="2" v-html="unit.TraitsDescription[index][language]"></td>
                    </tr>
                </template>
            </tbody>
            <tbody v-else>
                <tr v-if="unit.ActiveName.length">
                    <th scope="row" class="table-group-divider">
                        <span v-text="unit.ActiveName[language]"></span>
                        <span> / </span>
                        <span v-text="text.active"></span>
                        <br>
                        <span v-text="unit.cooldown" class="text-info"></span>
                        <span v-text="text.cd" class="text-info"></span>
                    </th>
                    <td v-html="unit.ActiveDescription[language][skillLevel - 1]"></td>
                </tr>
                <tr v-if="unit.PassiveName.length">
                    <th scope="row">
                        <span v-text="unit.PassiveName[language]"></span>
                        <span> / </span>
                        <span v-text="text.passive"></span>
                    </th>
                    <td v-html="unit.PassiveDescription[language][skillLevel - 1]"></td>
                </tr>
                <tr>
                    <th scope="col" colspan="2" style="width: 30%;">
                        <h4 v-text="text.trait"></h4>
                    </th>
                </tr>
                <template v-for="(key, index) in unit.TraitsName" :key="index">
                    <tr>
                        <th scope="row" :class="{ 'table-group-divider': index === 0 }">{{ unit.TraitsName[index][language] }}</th>
                        <td v-html="unit.TraitsDescription[index][language]"></td>
                    </tr>
                </template>
            </tbody>
        </table>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useInitUnit } from '@/store/InitUnit'
import { storeToRefs } from 'pinia'

export default {
    name: 'UnitSkill',
    components: {
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const { unit } = storeToRefs(useInitUnit())
        const skillLevel = ref(5)

        return {
            language,
            isMobile,
            text,
            unit,
            skillLevel,
        }
    }
}
</script>

<style scoped>
table {
    opacity: 0.9;
    table-layout:fixed;
}
</style>
