<template>
    <div class="offset-md-1 col">
        <div class="row justify-content-center">
            <UnitCard/>
            <ToolAccordion v-if="isMobile"/>
            <UnitStat/>
        </div>
        <SpineAnimation/>
        
        <ToolAccordion v-if="!isMobile"/>
        <UnitSkill/>
        <UnitDescription/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useInitUnit } from "@/store/InitUnit"
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'
import UnitCard from '@/components/UnitCard.vue'
import UnitStat from '@/components/UnitStat.vue'
import ToolAccordion from '@/components/ToolAccordion.vue'
import UnitSkill from '@/components/UnitSkill.vue'
import UnitDescription from '@/components/UnitDescription.vue'
import SpineAnimation from '@/components/SpineAnimation.vue'

export default {
    name: 'MainUnit',
    components: {
        UnitCard,
        UnitStat,
        ToolAccordion,
        UnitSkill,
        UnitDescription,
        SpineAnimation,
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { isSpine } = storeToRefs(useSpine())
        const { unit } = storeToRefs(useInitUnit())
        return {
            isMobile,
            isSpine,
            unit,
        }
    }
}
</script>

<style scoped></style>
