<template>
    <div class="col-md-4 offset-md-1 col-11" style="text-align: center;">
        <div id="ui-card-carousel" class="carousel slide carousel-fade mx-auto" data-bs-ride="carousel" style="width:70%;">
            <div class="carousel-inner" id="ui-card">
                <template v-for="(key, index) in unit.Imgs" :key="index">
                    <div class="carousel-item" :class="{'active': index === 0}" :id="`ui-card-carousel-item-${index + 1}`">
                        <img :src="`img/UI_Card/${unit.Imgs[index]}`" class="d-block w-100" alt="">
                    </div>
                </template>
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#ui-card-carousel" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#ui-card-carousel" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
            </button>
        </div>
        <h3 class="my-3">{{ unit.Name[language] }}</h3>
        <div class="btn-group" role="group" v-if="!isMobile">
            <button type="button" class="btn btn-secondary mb-3" @click="setSpine('spine_FULL')" v-if="!isSpine">SHOW ANIMATION</button>
            <button type="button" class="btn btn-secondary mb-3" @click="isSpine = !isSpine" v-if="isSpine">HIDE ANIMATION</button>
        </div>
        <div id="spine-SD-carousel" class="carousel slide mx-auto" data-bs-ride="false">
            <div class="carousel-indicators">
                <template v-for="(key, index) in unit.SD" :key="index">
                    <button type="button" data-bs-target="#spine-SD-carousel" :data-bs-slide-to="`${index}`" :class="{'active': index === 0}" aria-current="true" :aria-label="`Slide ${index + 1}`"></button>
                </template>
            </div>
            <div class="carousel-inner" id="spine-SD">
                <template v-for="(key, index) in unit.SD" :key="index">
                    <div class="carousel-item" :class="{'active': index === 0}" :id="`spine-SD-carousel-item-${index + 1}`">
                        <div :id="`spine_SD_${index}`" class="spine-div"></div>
                        <div class="carousel-caption d-none d-md-block">
                            <h5></h5>
                        </div>
                    </div>
                </template>
            </div>
            <button class="carousel-control-prev" type="button" data-bs-target="#spine-SD-carousel" data-bs-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Previous</span>
            </button>
            <button class="carousel-control-next" type="button" data-bs-target="#spine-SD-carousel" data-bs-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="visually-hidden">Next</span>
            </button>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { watch, onUpdated, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useInitUnit } from "@/store/InitUnit"
import { useLanguage } from '@/store/Language'
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'

export default {
    name: 'MainUnit',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language } = storeToRefs(useLanguage())
        const { isSpine } = storeToRefs(useSpine())
        const { unit } = storeToRefs(useInitUnit())

        const setSpine = (id) => {
            useSpine().setSpine(id, unit.value.Fullbody[0])
            isSpine.value = !isSpine.value
        }

        onMounted(() => {
            useSpine().setSpine_SD()
        })
        onUpdated(() => {
            useSpine().setSpine_SD()
        })

        return {
            language,
            isMobile,
            isSpine,
            unit,
            setSpine,
        }
    }
}
</script>

<style scoped>
.carousel-caption {
    position: relative;
}
.spine-div {
    /* position: absolute; */
    aspect-ratio: 4/3;
    z-index: 5;
}
</style>
